export function fetchWordmaps() {
    if (!fs.existsSync(wordmapsFolder)) {
        fs.mkdirSync(wordmapsFolder, { recursive: true });
    }
    const files = fs.readdirSync(wordmapsFolder);
    if (files.length === 0)
        return new Array();
    const wordMaps = parseWordmaps(files);
    wordmaps.set(wordMaps);
}
function parseWordmaps(files) {
    const wordmapsFiles = wordmapsInFiles(files);
    if (wordmapsFiles.length === 0)
        return new Array();
    return processWordmaps(wordmapsFiles);
}
function processWordmaps(files) {
    let wordmaps = [];
    files.forEach(file => {
        let wordmap = fs.readFileSync(wordmapsFolder + file, 'utf8');
        wordmaps.push(JSON.parse(wordmap));
    });
    return wordmaps;
}
function wordmapsInFiles(files) {
    const wordmaps = [];
    files.forEach(file => {
        if (file.endsWith(".wordmap.json")) {
            wordmaps.push(file);
        }
    });
    return wordmaps;
}
