<script lang="ts">
  import { openFilePrompt } from "../../../actions/open-file-prompt";
  import { notify } from "../../../stores/overlay";
  import { isFileImage } from "../../../util/file";

  let isImageSelected = false;
  let displayImage = "none";

  async function loadImage() {
    const image = await openFilePrompt();

    if (!isFileImage(image)) {
      notify(1000, "Invalid image file!");
      return;
    }

    isImageSelected = true;

    const preview = document.getElementById("preview") as HTMLImageElement;
    preview.src = image.path;

    displayImage = "block";
  }
</script>

<div
  on:click={() => loadImage()}
  class=" h-28 w-28 border-2 bg-black-light mx-3 rounded hover:bg-black-select hover:cursor-pointer transition-colors content-center items-center justify-center flex overflow-hidden"
>
  {#if !isImageSelected}
    <p class="text-white-darker absolute">Image...</p>
  {/if}

  <!-- svelte-ignore a11y-missing-attribute -->
  <img id="preview" class="h-full w-full" style={`display: ${displayImage};`} />
</div>
